p {
  color: #fff;
}

.NT-header-nav-dropdown {
  font-size: 16px;
  margin: auto 0.6rem;
  padding: 0 10px;
  color: black !important;
}
#collasible-nav-dropdown{
  color: black !important;

}
#nav-dropdown{
  color: black !important;
}
.NT-header-nav-dropdown > a:hover {
  color: rgb(236, 123, 16) !important;
}

.dropdown-menu {
  border-top: 5px solid rgb(236, 123, 16);
  background-color: transparent;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-bottom: none;
  border-left: none;
  border-right: none;
  margin-top: 30px !important;
}

.NT-header-nav-dropdown:hover {
  color: rgb(236, 123, 16) !important;
}

.NT-header-Submenu {
  font-size: 1.6rem;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
  padding-left: 15px;
  padding-right: 25px;
}

.NT-header-Submenu:hover {
  background-color: rgba(0, 0, 0, 0.6);
}

.NT-header-Submenu p {
  padding: 0;
  color: #fff;
  -webkit-transition: 0.3s ease;
  -o-transition: 0.3s ease;
  transition: 0.3s ease;
}

.NT-header-Submenu p:hover {
  -webkit-transform: translateX(10px);
      -ms-transform: translateX(10px);
          transform: translateX(10px);
  color: rgb(236, 123, 16);
}

.NT-header-nav-dropdown .NT-header-Submenu:first-child {
  margin-top: -5px;
  padding-top: 20px;
}

.NT-header-nav-dropdown .NT-header-Submenu:last-child {
  padding-bottom: 10px;
}

.NT-header-Submenu:hover {
  color: rgb(236, 123, 16);
}
.NT-header-sub-Submenu {
  font-size: 13px;
  background-color: rgba(0, 0, 0, 0.6);
}
.NT-header-sub-Submenu p {
  margin-top: 0px;
}

.sub-sub-menu .NT-header-sub-Submenu:first-child {
  padding-top: 20px;
  border-top: 5px solid rgb(236, 123, 16);
}

.sub-sub-menu .NT-header-sub-Submenu:hover {
  background-color: rgba(0, 0, 0, 0.6);
}

.sub-sub-menu .NT-header-sub-Submenu p {
  -webkit-transition: 0.3s ease;
  -o-transition: 0.3s ease;
  transition: 0.3s ease;
}

.sub-sub-menu {
  display: none;
  margin-top: -20px;
  margin-right: -150px;
  -webkit-transform: translatex(184px) translateY(-70px);
      -ms-transform: translatex(184px) translateY(-70px);
          transform: translatex(184px) translateY(-70px);
}

.sub-sub-menu:hover {
  display: block;
}

.supply:hover ~ .sub-sub-menu {
  display: block;
}

.sub-sub-menu .NT-header-sub-Submenu p:hover {
  color: rgb(236, 123, 16);
  -webkit-transform: translateX(5px);
      -ms-transform: translateX(5px);
          transform: translateX(5px);
}

.NT-header-toogle {
  background-color: white;
  border: none!important;
  -webkit-transform: scale(1.8);
      -ms-transform: scale(1.8);
          transform: scale(1.8);
  margin-top: 2rem!important;
}

.dropaanddown {
  background-color: rgba(0, 0, 0, 0.6) !important;
}
#menu-item{
  padding: 1.2rem 2rem;

}
.NT-header-image{
margin-top:-3rem;
position:relative;

}

/* /responsive */
@media only screen and (max-width: 1199px) {
.NT-header-image{
  margin-top:-1.5rem;
  -webkit-transform:scale(0.9);
      -ms-transform:scale(0.9);
          transform:scale(0.9);

}

  .NT-header-nav-dropdown {
    font-size: 16px;
    padding: 0 1px;
  }

  .sub-sub-menu {
    display: none;
    margin-top: 7.5rem;
    -webkit-transform: translatex(10px) translateY(-70px);
        -ms-transform: translatex(10px) translateY(-70px);
            transform: translatex(10px) translateY(-70px);
  }


}



