.label {
  position: relative;
  background-color: #ffa743;
  width: 80px;
  height: 40px;
  border-radius: 50px;
  cursor: pointer;
  display: inline-block;
  margin: 0 15px 0;
}

.toggle:checked + .label {
  background-color: #57a70b;
}

.ball {
  background-color: #fff;
  height: 34px;
  width: 34px;
  border-radius: 50%;
  position: absolute;
  top: 3px;
  left: 3px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-animation: slideOff 0.3s linear forwards;
          animation: slideOff 0.3s linear forwards;
}

.toggle:checked + .label .ball {
  -webkit-animation: slideOn 0.3s linear forwards;
          animation: slideOn 0.3s linear forwards;
}

@-webkit-keyframes slideOn {
  0% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }

  50% {
    -webkit-transform: translateX(20px) scale(1.2);
            transform: translateX(20px) scale(1.2);
  }

  100% {
    -webkit-transform: translateX(40px) scale(1);
            transform: translateX(40px) scale(1);
  }
}

@keyframes slideOn {
  0% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }

  50% {
    -webkit-transform: translateX(20px) scale(1.2);
            transform: translateX(20px) scale(1.2);
  }

  100% {
    -webkit-transform: translateX(40px) scale(1);
            transform: translateX(40px) scale(1);
  }
}

@-webkit-keyframes slideOff {
  0% {
    -webkit-transform: translateX(40px) scale(1);
            transform: translateX(40px) scale(1);
  }

  50% {
    -webkit-transform: translateX(20px) scale(1.2);
            transform: translateX(20px) scale(1.2);
  }

  100% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
}

@keyframes slideOff {
  0% {
    -webkit-transform: translateX(40px) scale(1);
            transform: translateX(40px) scale(1);
  }

  50% {
    -webkit-transform: translateX(20px) scale(1.2);
            transform: translateX(20px) scale(1.2);
