.slogan {
  position: absolute;
  font-family: Century Gothic;
  margin-top: 5.8vw;
  font-size: 5.7vw;
  font-weight: bold;
  text-align: center;
  margin-right: 0.2rem;
  color: #fff;
  text-decoration: underline;
  -webkit-text-decoration-color: transparent;
          text-decoration-color: transparent;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  padding-left: 30px;
  line-height: 8vw;
}
.slogan2 {
  position: absolute;
  margin-top: -89%;
  margin-left: 10px;
  font-family: Century Gothic;
  font-size: 5.3vw;
  font-weight: bold;
  text-align: center;
  color: #fff;
  text-decoration: underline;
  -webkit-text-decoration-color: transparent;
          text-decoration-color: transparent;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  padding-left: 25px;
  line-height: 8vw;
}

.banner1:hover .slogan {
  -webkit-text-decoration-color: #fff;
          text-decoration-color: #fff;
}

.banner1:hover .slogan2 {
  -webkit-text-decoration-color: #fff;
          text-decoration-color: #fff;
}

