/*Homepage msg-banner*/
#container-banner-msg {
  margin: 0px;
  text-align: center;
  font-weight: bolder;
  color: #616161;
  text-transform: uppercase;
  font-size: 3.5vmin;
  font-weight: bold;
  padding-top: 5vw;
  position: relative;
  width: 100%;
  display: block;
}

#flip-word {
  min-height: 5.5vh;
  max-height: auto;
  margin: 1vh 1vh;
  overflow: hidden;
}

#flip-word > div {
  color: #fff;
  padding: 0.5rem 0.5rem;
  margin: 0.1rem 0.1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
}

#flip-word div::before {
  content: 'knowledge';
  -webkit-animation: show-word 10s linear infinite;
          animation: show-word 10s linear infinite;
  padding: 10px 10px;
}

@-webkit-keyframes show-word {
  0% {
    content: 'knowledge';
    background: #fe7e0f;
  }
  10% {
    content: 'knowledge';
    background: #fe7e0f;
  }
  20% {
    content: 'technology';
    background: #74c707;
  }
  35% {
    content: 'technology';
    background: #74c707;
  }
  45% {
    content: 'marketing';
    background: #ffd400;
  }

  55% {
    content: 'marketing';
    background: #ffd400;
  }
  65% {
    content: 'extension services';
    background: #fe7e0f;
  }
  70% {
    content: 'extension services';
    background: #fe7e0f;
  }
  85% {
    content: 'improved planning';
    background: #74c707;
  }
  95% {
    content: 'improved planning';
    background: #74c707;
  }
  100% {
    content: 'improved planning';
    background: #92d638;
  }
}

@keyframes show-word {
  0% {
    content: 'knowledge';
    background: #fe7e0f;
  }
  10% {
    content: 'knowledge';
    background: #fe7e0f;
  }
  20% {
    content: 'technology';
    background: #74c707;
  }
  35% {
    content: 'technology';
    background: #74c707;
  }
  45% {
    content: 'marketing';
    background: #ffd400;
  }

  55% {
    content: 'marketing';
    background: #ffd400;
  }
  65% {
    content: 'extension services';
    background: #fe7e0f;
  }
  70% {
    content: 'extension services';
    background: #fe7e0f;
  }
  85% {
    content: 'improved planning';
    background: #74c707;
  }
  95% {
    content: 'improved planning';
    background: #74c707;
  }
  100% {
    content: 'improved planning';
    background: #92d638;
  }
}
